import forumFooter from "../../components/forumfooter.vue";
var app = require("../../common/common.js");
var per_page = 0;
var isfirst = true;
var catid = 0;
var gid = 0;
var activeClass = "tabs-border-active";
export default {
	components: {
		forumFooter
	},
	data: function() {
		return {
			isCard: true,
			isPosition: false,
			pageLoad: false,
			pageHide: false,
			pageData: {},
			systems: uni.getSystemInfoSync(),
			defaultActive: activeClass,
			TabCur: 0,
			scrollLeft: 0,
			tabsBorder: 0,
			swiperList: [{
				id: 0,
				type: 'image',
				url: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big84000.jpg'
			}, {
				id: 1,
				type: 'image',
				url: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big37006.jpg',
			}, {
				id: 2,
				type: 'image',
				url: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big39000.jpg'
			}, {
				id: 3,
				type: 'image',
				url: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big10001.jpg'
			}, {
				id: 4,
				type: 'image',
				url: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big25011.jpg'
			}, {
				id: 5,
				type: 'image',
				url: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big21016.jpg'
			}, {
				id: 6,
				type: 'image',
				url: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big99008.jpg'
			}]
		}

	},
	onReady: function() {
		
	},
	onLoad: function(option) {
		gid = option.gid;
		catid = option.catid;
		uni.setNavigationBarTitle({
			title: '帖子'
		});
		
		
		
		this.getPage();
	},

	onShow: function() {
		if (this.pageHide) {
			this.pageHide = false;
			this.getPage();
		}
	},
	onHide: function() {
		this.pageHide = true;
	},
	onReachBottom: function() {
		this.getList();
	},
	onPullDownRefresh: function() {
		this.refresh();
	},
	computed: {
		// 计算属性的 getter
		reversedMessage: function () {
			let self = this;
			let view = uni.createSelectorQuery().in(this).select(".tabs-border");
			if (self.tabsBorder != 0) {
				view.boundingClientRect(data => {
					self.tabsBorder = data.top;
				}).exec();
			}else{
				self.tabsBorder = 1;
			}
			
		  return self.tabsBorder
		}
	},
	watch: {
		reversedMessage: {
			handler: function(newVal, oldVal) {
			},
			deep: true
		}
	},
	onPageScroll: function(e) {  
	  let top = e.scrollTop;
	  if (top > 185) {
	  	this.isPosition = true;
	  } else {
	  	this.isPosition = false;
	  }
	},
	methods: {
		IsCard(e) {
			this.isCard = e.detail.value
		},
		getPage: function() {
			var that = this;
			uni.request({
				url: app.apiHost + "/module.php?m=forum&a=list&ajax=1",
				data: {
					gid: gid,
					catid: catid,
					authcode: app.getAuthCode()
				},
				success: function(res) {
					//登录
					if (res.data.error == 1000) {
						uni.navigateTo({
							url: "/pages/login/index",
						})
					} else {
						isfirst = false;
						that.pageLoad = true;
						that.pageData = res.data.data;
						per_page = res.data.data.per_page;
						uni.setNavigationBarTitle({
							title: that.pageData.group.title
						});
					}

				}
			})
		},
		setCat: function(cid) {
			catid = cid;
			isfirst = true;
			per_page = 0;
			if (catid == 0) {
				this.defaultActive = activeClass;
			} else {
				this.defaultActive = "";
			}
			var catlist = this.pageData.catlist;
			for (var i in catlist) {
				if (catlist[i].catid == catid) {
					catlist[i].isactive = 1;
				} else {
					catlist[i].isactive = 0;
				}
			}
			this.pageData.catlist = catlist;
			this.getList();
		},
		getList: function() {
			var that = this;
			if (!isfirst && per_page == 0) return false;
			uni.request({
				url: app.apiHost + "/module.php?m=forum&a=list&ajax=1",
				data: {
					per_page: per_page,
					catid: catid,
					gid: gid,
					authcode: app.getAuthCode()
				},
				success: function(res) {

					if (!res.data.error) {
						if (isfirst) {
							that.pageData.list = res.data.data.list;
							isfirst = false;
						} else {

							that.pageData.list = app.json_add(that.pageData.list, res.data.data.list);
						}
						per_page = res.data.data.per_page;

					}


				}
			})
		},
		goForum: function(id) {
			uni.navigateTo({
				url: "../forum/show?id=" + id
			})
		},
		refresh: function() {
			this.getPage();
			setTimeout(function() {
				uni.stopPullDownRefresh();
			}, 1000)
		},
		loadMore: function() {
			this.getList();
		},
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60;
			console.log(this.TabCur)
		},
		scrolltoupper() {
			console.log(22222222222222222222)
		}
	},
}
